@import url("http://fonts.googleapis.com/css2?family=Lato&display=swap");

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Lato", "sans-serif";
    /* overflow-x: hidden; */
    position: relative;
}

.about,
.projects,
.resume,
.contact {
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
}

@media screen and (max-width: 768px) {
    * {
      overflow: hidden;
    }
  }